import { Button, makeStyles, Typography } from "@material-ui/core";
import {
  LazyBg,
  LazyImage,
  SimpleLoader,
  IDOMetaDataRow,
  CountDownTimer,
} from "components";
import { getTokenFromAddress } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { useIDO } from "helpers";
import { transparentize } from "polished";
import React, { useState } from "react";
import { formatBigNumber, numberWithCommas } from "utils";
import { getIDOStatus } from "utils/ido";
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight";
import { IDOStatus } from "utils/enums";
import clsx from "clsx";
import { NavLink } from "react-router-dom";
import { IToken } from "types";
import moment from "moment-timezone";
import { NULL_ADDRESS } from "config/constants";

const AVATAR_WIDTH = 100;

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: AVATAR_WIDTH / 2,
    position: "relative",
    margin: "0 5px 16px 5px",
  },
  content: {
    paddingTop: AVATAR_WIDTH / 2,
    backgroundColor: theme.colors.neutralLight,
  },
  avatarWrapper: {
    position: "absolute",
    top: -AVATAR_WIDTH / 2,
    left: `calc(50% - ${AVATAR_WIDTH / 2}px)`,
  },
  avatar: {
    width: AVATAR_WIDTH,
    height: AVATAR_WIDTH,
    borderRadius: "50%",
  },
  main: { padding: 16 },
  header: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-start",
    marginBottom: 16,
  },
  title: {
    color: theme.colors.neutralLighter,
    fontWeight: 600,
    fontSize: 24,
    marginRight: 8,
  },
  symbol: {
    color: theme.colors.neutralDark,
    fontSize: 14,
    backgroundColor: transparentize(0.2, theme.colors.neutralLighter),
    padding: "4px 10px",
    marginTop: 12,
    fontWeight: 700,
    letterSpacing: 1,
  },
  group: {
    "&+&": {
      marginTop: 16,
      marginBottom: 16,
    },
  },
  label: { color: theme.colors.neutralLighter, fontWeight: 500, fontSize: 12 },
  value: { color: theme.colors.neutralLighter, fontWeight: 600, fontSize: 18 },
  linkWrapper: {
    display: "flex",
    justifyContent: "center",
    marginTop: 16,
  },
  link: {
    display: "inline-flex",
    color: theme.colors.primary,
    alignItems: "center",
    justifyContent: "center",
    position: "relative",
    textDecoration: "none",
    padding: "8px 12px",
    "&:hover": {
      textDecoration: "underline",
    },
  },
  statusLabel: {
    position: "absolute",
    top: 0,
    right: 0,
    padding: "2px 4px",
    border: `1px solid ${theme.colors.transparent}`,
    fontWeight: 600,
    fontSize: 14,
    "&.upcoming": {
      backgroundColor: transparentize(0.9, theme.colors.green),
      color: theme.colors.green,
    },
    "&.ongoing": {
      backgroundColor: transparentize(0.9, theme.colors.yellow),
      color: theme.colors.yellow,
    },
    "&.ended": {
      backgroundColor: transparentize(0.9, theme.colors.error),
      color: theme.colors.error,
    },
    "&.claimable": {
      backgroundColor: theme.colors.neutralLighter,
      color: theme.colors.neutralLight,
    },
  },
  progressWrapper: {
    position: "relative",
    backgroundColor: theme.colors.neutralLight,
    marginBottom: 16,
    marginTop: 16,
    height: 30,
    borderRadius: 16,
    color: "#67634a",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontSize: 14,
    fontWeight: 600,
    border: `1px solid #67634a`,
    overflow: "hidden",
    "& span": {
      zIndex: 2,
    },
  },
  progress: {
    height: 30,
    position: "absolute",
    left: 0,
    top: 0,
    bottom: 0,
    backgroundColor: theme.colors.primary,
  },
}));

interface IProps {
  id: string;
}

export const IDOPreview = (props: IProps) => {
  const { data } = useIDO(props.id);
  const classes = useStyles();
  const [counter, setCounter] = useState(1);
  const { networkId } = useConnectedWeb3Context();
  const increateCounter = () => setCounter((counter) => counter + 1);

  const renderContent = () => {
    if (!data || !data.meta) return null;
    const { startTime, endTime, claimTime } = data;
    const status = getIDOStatus(data);
    let fundToken: IToken | null = null;

    try {
      fundToken = getTokenFromAddress(data.fundToken, networkId);
    } catch (error) {}

    if (!fundToken) return null;

    const percent =
      data.fundRaised.mul(10000).div(data.fundTarget).toNumber() / 100;

    return (
      <>
        <div className={classes.main}>
          <div className={classes.header}>
            <Typography className={classes.title}>{data.meta.title}</Typography>
            <Typography className={classes.symbol}>
              {data.meta.tokenSymbol}
            </Typography>
          </div>
          <IDOMetaDataRow data={data.meta} />
          <Typography
            className={clsx(
              classes.statusLabel,
              status === IDOStatus.Upcoming && "upcoming",
              status === IDOStatus.Ongoing && "ongoing",
              status === IDOStatus.Ended && "ended",
              status === IDOStatus.Claimable && "claimable"
            )}
          >
            {status === IDOStatus.Upcoming && "Coming soon"}
            {status === IDOStatus.Ongoing && "Live"}
            {status === IDOStatus.Ended && "Ended"}
            {status === IDOStatus.Claimable && "Claimable"}
          </Typography>
          <div className={classes.group}>
            <Typography className={classes.label}>
              {status === IDOStatus.Upcoming && "IDO starts"}
              {status === IDOStatus.Ongoing && "IDO ends"}
              {status === IDOStatus.Ended &&
                data.saleToken !== NULL_ADDRESS &&
                "IDO claims"}
              {status === IDOStatus.Claimable &&
                data.saleToken !== NULL_ADDRESS &&
                "Total claimed"}
            </Typography>

            {status === IDOStatus.Upcoming && (
              <CountDownTimer
                destTimeStamp={startTime * 1000}
                onReached={increateCounter}
              />
            )}
            {status === IDOStatus.Ongoing && (
              <CountDownTimer
                destTimeStamp={endTime * 1000}
                onReached={increateCounter}
              />
            )}
            {status === IDOStatus.Ended && data.saleToken !== NULL_ADDRESS && (
              <CountDownTimer
                destTimeStamp={claimTime * 1000}
                onReached={increateCounter}
              />
            )}
            {status === IDOStatus.Claimable && data.saleToken !== NULL_ADDRESS && (
              <Typography className={classes.value}>
                {numberWithCommas(
                  formatBigNumber(
                    data.totalReleased,
                    data.meta.tokenDecimals,
                    0
                  )
                )}
                /
                {numberWithCommas(
                  formatBigNumber(data.saleRaised, data.meta.tokenDecimals, 0)
                )}{" "}
                {(data.meta.tokenSymbol || "").toUpperCase()}
              </Typography>
            )}
          </div>
          {status !== IDOStatus.Upcoming && (
            <div className={classes.progressWrapper}>
              <span>{percent}% sold</span>
              <div
                className={classes.progress}
                style={{ width: `${Math.min(percent, 100)}%` }}
              />
            </div>
          )}
          <div className={classes.group}>
            <Typography className={classes.label}>Start Time</Typography>
            <Typography className={classes.value}>
              {moment(new Date(startTime * 1000))
                .utc()
                .format("MM/DD/YYYY HH:mm")}{" "}
              GMT
            </Typography>
          </div>
          <div className={classes.group}>
            <Typography className={classes.label}>End Time</Typography>
            <Typography className={classes.value}>
              {moment(new Date(endTime * 1000))
                .utc()
                .format("MM/DD/YYYY HH:mm")}{" "}
              GMT
            </Typography>
          </div>
          <div className={classes.group}>
            <Typography className={classes.label}>Total Sale Token</Typography>
            <Typography className={classes.value}>
              {numberWithCommas(
                formatBigNumber(data.saleTarget, data.meta.tokenDecimals, 0)
              )}{" "}
              {(data.meta.tokenSymbol || "").toUpperCase()}
            </Typography>
          </div>

          <div className={classes.group}>
            <Typography className={classes.label}>Total Fund Token</Typography>
            <Typography className={classes.value}>
              {numberWithCommas(
                formatBigNumber(data.fundTarget, fundToken.decimals, 0)
              )}{" "}
              {fundToken.symbol.toUpperCase()}
            </Typography>
          </div>
          <div className={classes.linkWrapper}>
            <NavLink className={classes.link} to={`/idos/${data.address}`}>
              Research
              <KeyboardArrowRightIcon />
            </NavLink>
          </div>
        </div>
      </>
    );
  };

  return (
    <div className={classes.root}>
      {!data || !data.meta ? null : (
        <LazyImage
          src={data.meta.logo}
          className={classes.avatarWrapper}
          imgClassName={classes.avatar}
        />
      )}
      <div className={classes.content}>
        {!data || !data.meta ? <SimpleLoader /> : renderContent()}
      </div>
    </div>
  );
};
