import { makeStyles, Typography, Button } from "@material-ui/core";
import { NotAvailable, PageWrapper, TierModal } from "components";
import { networkIds } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { useState } from "react";
import { IDOSection } from "utils/enums";
import { IDOSectionContent } from "./components";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import { SvgGradientBanner } from "components/SvgGradientBanner";

const useStyles = makeStyles((theme) => ({
  tier: {
    display: "flex",
    justifyContent: "flex-end",
    color: theme.colors.primary,
    textDecoration: "underline",
    fontWeight: 600,
    cursor: "pointer",
    transition: "all 0.4s",
    position: "absolute",
    right: 16,
    [theme.breakpoints.down("sm")]: {
      display: "flex",
      position: "relative",
      top: 12,
    },
    "&:hover": { opacity: 0.7 },
    paddingTop: 12,
    paddingBottom: 12,
  },
  applyContainer: {
    padding: 32,
    fontWeight: 600,
    minHeight: 200,
    textAlign: "center",
    background: theme.colors.neutralLight,
    position: "relative",
  },
  illustration: {
    position: "absolute",
    top: 0,
    right: 0,
    bottom: 0,
    pointerEvents: "none",
    "& svg": {
      height: "100%",
    },
  },
  apply: {
    display: "inline-flex",
    marginTop: 16,
    color: theme.colors.black,
    backgroundColor: theme.colors.primary,
    textDecoration: "none",
    padding: "8px 12px",
    fontWeight: 600,
    transition: "all 0.4s",
    "&:hover": {
      opacity: 0.7,
    },
  },
  tabsWrapper: {
    marginBottom: 16,
    [theme.breakpoints.down("md")]: {
      marginBottom: 0,
    },
  },
  tabsContainer: {
    width: "100%",
    textAlign: "center",
    margin: "10px auto",
    padding: 16,
    [theme.breakpoints.down("md")]: {
      padding: 0,
    },
  },
  tabsItem: {
    backgroundColor: theme.colors.neutralLight,
    color: theme.colors.primary,
  },
  indicator: {
    backgroundColor: theme.colors.primary,
  },
  button: {
    backgroundColor: theme.colors.primary,
    outline: "none",
    border: 0,
    marginTop: 12,
  },
}));

const dataTabs = [
  {
    id: 0,
    mode: IDOSection.Ended,
  },
  {
    id: 1,
    mode: IDOSection.Ongoing,
  },
  {
    id: 2,
    mode: IDOSection.Upcoming,
  },
];
interface IState {
  tierVisible: boolean;
}

const IDOPage = () => {
  const classes = useStyles();
  const { networkId } = useConnectedWeb3Context();
  const [value, setValue] = useState(0);
  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };
  const [state, setState] = useState<IState>({ tierVisible: false });
  const setTierVislbe = (tierVisible: boolean) =>
    setState((prev) => ({ ...prev, tierVisible }));

  if (networkId === networkIds.HARMONY) {
    return (
      <PageWrapper>
        <NotAvailable />
      </PageWrapper>
    );
  }

  return (
    <PageWrapper>
      <div>
        {state.tierVisible && (
          <TierModal onClose={() => setTierVislbe(false)} />
        )}
        <div className={classes.tier} onClick={() => setTierVislbe(true)}>
          My Tier?
        </div>
        <Typography align="center" component="h3" variant="h1">
          IDO's
        </Typography>
        <Box className={classes.tabsWrapper}>
          <Tabs
            value={value}
            onChange={handleChange}
            className={classes.tabsContainer}
            centered
            TabIndicatorProps={{ className: classes.indicator }}
          >
            {dataTabs.map((item, i) => {
              return (
                <Tab
                  label={item.mode}
                  className={classes.tabsItem}
                  disableRipple
                  key={i}
                />
              );
            })}
          </Tabs>
        </Box>
        {dataTabs.map((item) => {
          return (
            <IDOSectionContent
              mode={item.mode}
              value={value}
              index={item.id}
              key={item.id}
            />
          );
        })}
        <SvgGradientBanner
          title="Want to launch your project on LegioDao?"
          description=""
          btnTitle="Apply"
          typeformId="r5JLNVlb"
        />
      </div>
    </PageWrapper>
  );
};

export default IDOPage;
